<template>
  <div
    id="header"
    :class="[flag?'sticky': '']"
  >
    <div class="music-container">
      <ul class="header-container">
        <li class="item" @click="goIndex()">首页</li>
        <li class="item" @click="goSinger()">歌手</li>
        <li class="item" @click="goRank()">排行版</li>
        <li class="item" @click="goTV()">电台</li>
        <li class="item" @click="goMv()">MV</li>
      </ul>
    </div>
    </div>
</template>
<script>
export default {
  name: "NavHeader",
  props: {
    flag: Boolean,
  },
  methods: {
    goIndex() {
      this.$router.push({
        path: `/index`,
      })
    },
    goMv() {
      let time = new Date().getTime()
      this.$router.push({
        path: `/mv/${time}`,
      })
    },
    goSinger() {
      let time = new Date().getTime()
      this.$router.push({
        path: `/singer/${time}`,
      })
    },
    goRank() {
      let time = new Date().getTime();
      this.$router.push({
        path: `/rank/${time}`,
      })
    },
    goTV() {
      this.$message({
        showClose: true,
        message:"未开发",
        type: "success"
      });
    }
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
@import "../assets/scss/config.scss";
.sticky {
  position: sticky;
  top: 0;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
  z-index: 100;
}
#header {
  background: $colorD;
  transition: top 2s;
  .music-container {
    padding: 10px;
    .header-container {
      height: 40px;
      width: 1020px;
      margin: 0 auto;
      @include flex();
      .item {
        text-align: center;
        flex-grow: 1;
        @include font(16px, 20px, $colorB);
        cursor: pointer;
        &:hover {
          color: $colorA;
        }
      }
    }
  }
}
</style>

